<template>
  <div class="hud">
    <health class="hud__health"/>

    <timer class="hud__timer"/>

    <score class="hud__score"/>

    <cross-hair-icon class="hud__crosshair"/>

    <v-alert class="hud__alert" :duration="5000">
      <p>Move your phone to look around.</p>
      <p>Tap anywhere to shoot.</p>
    </v-alert>

    <v-button class="hud__reload" fab @click="reload">
      <reload-icon/>
    </v-button>

    <toggle-fullscreen class="hud__fullscreen"/>

    <paused-modal v-if="!isPlaying"/>
  </div>
</template>

<script>
import AFRAME from 'aframe';
import { mapGetters } from 'vuex';

import ReloadIcon from '@/components/common/icons/Reload.vue';
import CrossHairIcon from '@/components/common/icons/CrossHair.vue';
import VButton from '@/components/common/VButton.vue';
import VAlert from '@/components/common/VAlert.vue';
import Health from './components/Health.vue';
import Timer from './components/Timer.vue';
import Score from './components/Score.vue';
import ToggleFullscreen from './components/ToggleFullscreen.vue';
import PausedModal from './components/PausedModal.vue';

export default {
  name: 'Hud',
  components: {
    ReloadIcon,
    CrossHairIcon,
    VButton,
    VAlert,
    Health,
    Timer,
    Score,
    ToggleFullscreen,
    PausedModal,
  },
  computed: {
    ...mapGetters('game', [
      'isPlaying',
    ]),
  },
  methods: {
    reload() {
      AFRAME.scenes[0].emit('reload');
    },
  },
};
</script>

<style lang="scss" scoped>
.hud__health {
  position: fixed;
  top: 20px;
  left: 20px;
}

.hud__timer {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.hud__score {
  position: fixed;
  top: 20px;
  right: 20px;
}

.hud__crosshair {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hud__alert {
  position: fixed;
  top: 60%;
  left: 60%;
}

.hud__reload {
  position: fixed;
  bottom: 20px;
  left: 20px;
}

.hud__fullscreen {
  position: fixed;
