<template>
  <div class="fullscreenButton" @click="toggleFullScreen">
    <v-button v-if="isFullScreen" fab>
      <fullscreen-icon/>
    </v-button>
    <v-button v-else fab>
      <fullscreen-exit-icon/>
    </v-button>
  </div>
</template>

<script>
import FullscreenIcon from '@/components/common/icons/Fullscreen.vue';
import FullscreenExitIcon from '@/components/common/icons/FullscreenExit.vue';
import VButton from '@/components/common/VButton.vue';

export default {
  name: 'ToggleFullscreen',
  components: {
    FullscreenIcon,
    FullscreenExitIcon,
    VButton,
  },
  data() {
    return {
      isFullScreen: false,
    };
  },
  methods: {
    toggleFullScreen() {
      if (!document.fullscreenElement) {
        document.documentElement.requestFullscreen();
        this.isFullScreen = true;
      } else {
        document.exitFullscreen();
        this.isFullScreen = false;
      }
    },
  },
};
</script>
