/**
 * Bullet template component
 * Extracted from https://github.com/supermedium/aframe-super-shooter-kit/blob/master/index.js
 *
 * @property {float} damagePoints - Damage points inflicted on target.
 * @property {float} maxTime - Maximum time the bullet is alive.
 * @property {string} name - Bullet name.
 * @property {integer} poolSize - Size of the pool of bullets.
 * @property {float} speed - Speed of the bullet.
 */
import AFRAME from 'aframe';

AFRAME.registerComponent('bullet', {
  dependencies: ['material'],

  schema: {
    damagePoints: { default: 1.0, type: 'float' },
    maxTime: { default: 4.0, type: 'float' }, // seconds.
    name: { default: 'normal', type: 'string' },
    poolSize: { default: 10, type: 'int', min: 0 },
    speed: { default: 8.0, type: 'float' }, // meters / sec.
  },

  init() {
    const { el } = this;
    el.object3D.visible = false;
    el.addEventListener('object3dset', () => {
      el.sceneEl.systems.bullet.registerBullet(this);
    });
  },
});
