<template>
  <div class="game">
    <a-scene
      vr-mode-ui="enabled: false"
    >
      <a-assets ref="assets">
      </a-assets>

      <!-- Soundtrack -->
      <a-entity sound="src: #soundtrack;
                       positional: false;
                       autoplay: true;
                       loop: true">
      </a-entity>

      <!-- Player Bullets -->
      <a-entity
        bullet="name: normal; speed: 30.0; poolSize: 20"
        geometry="primitive: icosahedron; radius: 0.1;"
        material="color: #000; emissiveIntensity: 0.5; emissive: #000">
      </a-entity>

      <a-entity
        bullet="name: doubleBullets; speed: 30.0; poolSize: 20; damagePoints: 2.0"
        geometry="primitive: icosahedron; radius: 0.1;"
        material="color: #000; emissiveIntensity: 0.5; emissive: #000">
      </a-entity>

      <!-- Enemy Bullet -->
      <a-entity
        bullet="name: enemy; speed: 10.0; poolSize: 20"
        geometry="primitive: icosahedron; radius: 0.1;"
        material="color: #ff0000; emissiveIntensity: 0.5; emissive: #ff0000">
      </a-entity>

      <!-- Enemies -->
      <a-entity
        v-for="enemy in game.enemies"
        :key="enemy._id"
        class="enemies"
        :animation-mixer="`clip: ${game.animationClips.enemyIdle || '*'}`"
        :target="`static: false;
                  ignoreBullets: enemy;
                  healthPoints: ${enemy.shotsToDie}`"
        :enemy="`assetId: ${enemy.assetId};
                 playerDamage: ${enemy.playerDamage};
                 score: ${enemy.score};
                 spawnProbability: ${enemy.spawn.probability};
                 spawnMinScore: ${enemy.spawn.minScore};
                 spawnMaxScore: ${enemy.spawn.maxScore}`"
        random-spawn="min: -10 -1 -10;
                      max: 10 2 10;
                      radius: 2"
        visible="false"
        animation="property: position;
                  to: 0 1.6 0;
                  dur: 20000;
                  easing: easeInOutCubic;
                  startEvents: spawn;
                  pauseEvents: despawn"
        proxy-event__spawn="event: spawn; to: CHILDREN"
        proxy-event__despawn="event: despawn; to: CHILDREN"
        camera="active: false"
        look-at="#player"
      >
        <a-entity
          shooter="activeBulletType: enemy"
          :enemy-shooter="`shootingDelay: ${enemy.shootingDelay};
                           shootingDistance: ${enemy.shootingDistance}`"
          position="0 0 -1"
        >
        </a-entity>
      </a-entity>

      <!-- Powerups -->
      <a-entity
        v-for="powerup in game.powerUps"
        :key="powerup._id"
        target="static: false"
        :powerup="`type: ${powerup.type};
                   assetId: ${powerup.assetId};
                   displayDuration: ${powerup.displayDuration};
                   spawnProbability: ${powerup.spawnProbability}`"
        random-spawn="min: -10 -1 -10;
                      max: 10 2 10;
                      radius: 2"
        visible="false"
        scale="5 5 5"
      ></a-entity>

      <!-- Player -->
      <a-entity
        id="player"
        sphere-collider="objects: .enemies; radius: 0.5"
        target
        geometry="primitive: sphere; radius: 0.5"
        position="0 1.6 0"
        proxy-event="event: die; to: #gun;"
      >
        <a-camera
          active="true"
          position="0 0 0"
        >
          <a-entity
            id="gun"
            position="0 0 -1"
            shooter="activeBulletType: normal"
            player="ammo: 10"
            :animation-mixer="`clip: ${game.animationClips.gunFire || '*'}`"
          >
          </a-entity>
        </a-camera>
      </a-entity>

      <a-entity
        sound="src: #shootSound;
               positional: false;
               on: shoot;
               poolSize: 10"
        proxy-event="event: shoot; from: #gun"
      >
      </a-entity>
    </a-scene>

    <component :is="hudComponent"></component>
  </div>
</template>

<script>
// Aframe packages
import 'aframe';
import 'aframe-extras';
import 'aframe-proxy-event-component';
import 'aframe-look-at-component';

// Aframe Components
import './components/player';
import './components/enemy';
import './components/enemyShooter';
import './components/randomSpawn';
import './components/powerup';

// Super-shooter-kit
import './components/bullet';
import './components/shooter';
import './components/target';
import './systems/bullet';

// Vue
import Hud from '@/components/Hud/Hud.vue';
import { mapGetters, mapActions } from 'vuex';

// Utils
import camera from './utils/camera';

export default {
  name: 'Game',
  components: {
    Hud,
  },
  computed: {
    ...mapGetters('config', [
      'game',
    ]),
  },
  data() {
    return {
      hudComponent: undefined,
    };
  },
  methods: {
    ...mapActions('game', [
      'setIsPlaying',
      'setTime',
      'setHealth',
    ]),
    getAssetEl(asset) {
      if (asset.type === 'Model') {
        const modelEl = document.createElement('a-asset-item');

        modelEl.id = asset.id;
        modelEl.setAttribute('src', asset.src);

        return modelEl;
      }

      if (asset.type === 'Sound') {
        const soundEl = document.createElement('audio');

        soundEl.id = asset.id;
        soundEl.setAttribute('src', asset.src);
        soundEl.setAttribute('preload', 'auto');

        return soundEl;
      }

      return false;
    },
    setAssets() {
      const { assets } = this.$refs;

      this.game.assets.forEach((asset) => {
        const assetEl = this.getAssetEl(asset);

        if (assetEl) {
          assets.appendChild(assetEl);
        }
      });
    },
    setGameProperties() {
      this.setAssets();
      this.setTime(this.game.singlePlayerMode.gameDuration);
      this.setHealth(this.game.initialHealthLevel);
      this.setIsPlaying(true);
    },
  },
  mounted() {
    const { assets } = this.$refs;
    const hasConfig = Object.values(this.game).length > 0;

    if (hasConfig) {
      this.setGameProperties();
    } else {
      this.$router.push({ path: '/' });
    }

    assets.addEventListener('loaded', () => {
      this.hudComponent = Hud;
      camera.init();
    });
  },
  beforeDestroy() {
    camera.stopStream();
  },
};
</script>
