/**
 * Target component
 * Extracted from https://github.com/supermedium/aframe-super-shooter-kit/blob/master/index.js
 *
 * @property {boolean} active - Boolean if the target is active/inactive.
 * @property {float} healthPoints - Amount of health points the target has.
 * @property {boolean} static - If target is static bounding box is calculated once.
 * @property {array} ignoreBullets - Bullets to ignore collision with target.
 */

import AFRAME from 'aframe';

AFRAME.registerComponent('target', {
  schema: {
    active: { default: true },
    healthPoints: { default: 1, type: 'float' },
    static: { default: true },
    ignoreBullets: { type: 'array' },
  },

  init() {
    const { el } = this;
    el.addEventListener('object3dset', () => {
      el.sceneEl.systems.bullet.registerTarget(this, this.data.static);
    });
  },

  update() {
    // `this.healthPoints` is current hit points with taken damage.
    // `this.data.healthPoints` is total hit points.
    this.healthPoints = this.data.healthPoints;
  },

  /**
   * Take damage.
   */
  onBulletHit(bullet) {
    const ignoreHit = this.data.ignoreBullets.some((item) => item === bullet.type);

    if (!this.data.active) { return; }
    if (ignoreHit) { return; }

    this.lastBulletHit = bullet;
    this.healthPoints -= bullet.damagePoints;
    if (this.healthPoints <= 0) { this.el.emit('die'); }
  },
});
