/**
 * Player
 *
 * Implement the player mechanics, such as clickToShoot and damage.
 *
 * @property {number} ammo - Number of shots the player has (ammunition).
 * @property {number} hitDamage - Damage taken by each hit of enemy.
 *
 */

import AFRAME from 'aframe';
import store from '@/store';
import { powerUpTypes } from '@/helpers/enums';

AFRAME.registerComponent('player', {
  schema: {
    ammo: { type: 'number', default: 5 },
    hitDamage: { type: 'number', default: 10 },
  },

  init() {
    const { el, data } = this;
    this.ammo = data.ammo;

    // Set gun model
    el.setAttribute('gltf-model', '#gunModel');

    this.shoot = this.shoot.bind(this);
    el.sceneEl.addEventListener('mousedown', this.shoot);

    this.reload = this.reload.bind(this);
    el.sceneEl.addEventListener('reload', this.reload);

    // Used die event instead of hit because collision with enemy emits the same event
    this.die = this.die.bind(this);
    el.addEventListener('die', this.die);
  },

  playAnimation() {
    const { el } = this;
    const { animationClips } = store.getters['config/game'];

    if (animationClips.gunFire) {
      el.setAttribute('animation-mixer', {
        loop: 'once',
        clip: animationClips.gunFire,
      });
    }
  },

  shoot() {
    if (this.ammo > 0) {
      this.ammo -= 1;
      this.changeBullets();
      this.el.emit('shoot');
      this.playAnimation();
    } else {
      this.el.sceneEl.removeEventListener('mousedown', this.shoot);
    }
  },

  changeBullets() {
    const { activeBulletType } = this.el.components.shooter.data;
    const hasDoubleBullets = store.getters['game/activePowerups'].includes(powerUpTypes.DOUBLE_BULLETS);

    // If enemy has doubleBullets powerup change activeBullet of player
    if (hasDoubleBullets && activeBulletType !== 'doubleBullets') {
      this.el.setAttribute('shooter', 'activeBulletType', 'doubleBullets');
    }

    if (!hasDoubleBullets && activeBulletType !== 'normal') {
      this.el.setAttribute('shooter', 'activeBulletType', 'normal');
    }
  },

  reload() {
    this.ammo = this.data.ammo;

    this.el.sceneEl.addEventListener('mousedown', this.shoot);
  },

  die() {
    const { data } = this;

    store.dispatch('game/decrementHealth', data.hitDamage);
  },

  remove() {
    this.el.sceneEl.removeEventListener('mousedown', this.shoot);
    this.el.sceneEl.removeEventListener('reload', this.reload);
  },
});
